
*{
	margin: 0px;
	padding: 0px;
}
#app{
	height: 100%;
	width: 100%;
}
.content-body{
	width: auto;
	height: auto;
	margin: 10px;
	padding-bottom: 20px;
}
.line-title{
	width: 100%;
	height: 70px;
	line-height: 70px;
	font-size: 22px;
}
.text {
  font-size:14px;
  color:#606266;
}
.select-txt {
  /*width:120px;*/
  /*text-align: right;*/
  padding: 10px;
}
.el-form-item{
	margin-bottom: 25px;
}
.el-checkbox{
	margin-right:30px;
	margin-left:0;
}
.el-checkbox+.el-checkbox{
	margin-right:30px;
	margin-left:0;
}
.el-radio{
	margin-right:30px;
	margin-left:0;
	height:40px;
	line-height: 40px;
}
.el-radio+.el-radio{
	margin-right:30px;
	margin-left:0;
}

.div_center {
    text-align: center;;
    width:100%;
    margin:0 auto;
    margin-bottom: 20px;
}
.phoneNameBox{
  width:50%;
  margin-left:8%;
  border:1px solid #b3d8ff;
  min-height: 90px;
  border-radius:5px;
}
.phoneNameText{

  padding:0 10px;
  height:30px;
  line-height: 30px;
}
.phoneNameText+.phoneNameText{
  border-top:1px solid #b3d8ff;
}
.phoneNameText:nth-child(2n){
   background: #ecf5ff;
}

.logic-select {
  width:100px;
}
.conditionBox{
  width:88%;
  margin:10px auto;
}
.btnAdd,.upload-file{
  margin-left:15px;
}
.el-upload__tip{
  margin-left:5px;
}
.taskBox{
  width:40%;
  border:1px solid #c0c4cc;
  padding:5px;
}
.taskBox:hover{
  border:1px solid #409EFF;
}
.conditionType{
  width:98%;
  height: auto;
  max-height:500px;
  overflow: hidden;
  position: relative;
  margin-bottom: 30px;
}
.searchType,.searchList{
  width:104%;
  height: auto;
  max-height: 500px;
  overflow-x:hidden;
  overflow-y: scroll;
}

.tip_red{
  color:red;
}
.tip_green{
  color:green;
}
.saveData{
  margin-top:20px;
}
















































































































































































































































































































































































































































































































































